@import url('https://fonts.googleapis.com/css2?family=Single+Day&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

:root{
    --primary-green:#72DB73;
}
#createAccount{
    width: 100%;
    min-height: 100vh;
    display: flex;
}

#createAccount .left,#createAccount .right{
    width: 50%;
    position: relative;
}

#createAccount .left{
    position: fixed;
    top: 0%;
    left: 0%;
}
#createAccount .right{
    margin-left: 50%;
}

#createAccount .left img{
    width: 100%;
    height: 100vh;

}

#createAccount .left p{
    position: absolute;
    width: 100%;
    padding-left: 40px;
    bottom: 30px;
    text-align: left;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 3.6rem;
    line-height: 141.69%;

    letter-spacing: 0.02em;
}

#createAccount .right{
    background: #000000;
    padding: 60px;
    box-sizing: border-box;
}

#createAccount .right-head{
    text-align: center;
}

.superApp{
    color: #72DB73;
    font-size: 45px;
    margin: 0%;
    font-family: 'Single Day', cursive;
}

#createAccount .right-head p{
    color: white;
}

#createAccount .right-box{
    margin: auto;
    width: 400px;
    max-width: 100%;
}

#createAccount form input.textField{
    background: #292929;
    width: 100%;
    height: 42px;
    color: #7C7C7C;
    border-radius: 3.81584px;
    border: none;
    padding-left:17px;
    font-size:0.9rem;
}

.signupBtn{
    background: var(--primary-green);
    color: #fff;
    border-radius: 28px;
    width: 100%;
    height: 37px;
    border: none;
}

.text-green{
    color: var(--primary-green);
}

#createAccount .right-box .text-small{
    font-size: 0.9rem;
}

.right-box .error-text{
    display: none;
}
.right-box .showError .error-text{
    color: #ff0000;
    font-size: 0.8rem;
    margin-top: 5px;
    display: block;
}

.right-box .showError .error-field{
    border: 1px solid #FF0000 !important;
}


@media screen and (max-width:1100px){
    #createAccount .left p {
        font-size: 2.5rem;
    }
}

@media screen and (max-width:768px) {
    #createAccount .left{
        display: none;
    }
    #createAccount .right{
        margin-left: 0%;
        width: 100%;
    }
}