#profilePage {
    width: 100%;
    /* min-height: 100vh; */
    background-color: #000;
}

.userBox{
    background: #5746EA;
    width: 100%;
    border-radius: 33.428px;
    
}

.userBox img{
    width: 125px;
}

.userBox .categoryTags div{
    width: 45%;
    padding: 0.6rem 1.5rem;
    background: #9F94FF;
    border-radius: 36.9467px;
}

.userBox .user_name,.userBox .user_email{
    font-size: 1.2rem;
}

.userBox .user_userName{
    font-size:1.8rem;
    font-weight: 600;
}

#weather-box{
    border-radius: 33px;
    overflow: hidden;
}

.mt1_7{
    margin-top: 1.7rem;
}

#weather-box #time{
    background: #FF4ADE;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4rem;
}
#weather-box #time h1{
    font-size: 1.6rem;
}
#weather-box #weather{
    background: #101744;;
}

#weather .custom-lines{
    position: relative;
}

#weather .custom-lines::after,#weather .custom-lines::before {
    content: "";
    position: absolute;
    width: 1px;
    height: 40px;
    background-color: #fff;
    top: 50%;
    transform: translateY(-50%);
}

#weather .custom-lines::before {
    left: 0;
}
#weather .custom-lines::after{
    right: 0;
}


#profilePage .right-box{
    height: 100%;
    border-radius: 19px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

#profilePage .right-box .opacity-low{
    position: absolute;
    bottom: 0%;
    left: 0%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.74);
    color: #fff;
}

#profilePage .right-box img{
    width: 100%;
    /* height: 450px; */
    height: 100%;

}

