#categoryPage{
    width: 100%;
    min-height: 100vh;
    background-color: #000;
}

.categories{
    gap: 6px;
}

.categoryHeading{
    font-size: 3rem;
    line-height: 4rem;
}

.categoryCard{
    width: 200px;
    height: 200px;
    border: 6px solid #000000;
    border-radius: 20px;
    padding: 15px 10px;
}

.categoryCard.selectedCategory{
    border-color: #11B800;
}


.categoryCard *{
    pointer-events: none;
}

.categoryCard .head{
    font-weight: 500;
    font-size: 22px;
    line-height: 47px;
    color: #FFFFFF;
    margin-bottom: 10px;
}

.categoryCard img{
    width: 100%;
}

.selected-tags div{
    background: #148A08;
    border-radius: 31px;
    color:white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 140px;
    padding: 7px 17px;
}

.selected-tags div span{
    color: #085C00;
    cursor:pointer;
}

.nextPage{
    background: #148A08;
        border-radius: 31px;
        color: white;
        width: 140px;
        padding: 7px 17px;
        cursor:pointer;
}

.Mincategory{
    color: red;
}

.Mincategory img{
    width: 18px;
}